.header {
  padding-top: 14px;
  padding-bottom: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__item--boxes {
  display: none;
}

.header__nav-link {
  font-family: "Manrope";
  color: $black;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.5px;
}

.header__button-link {
  padding: 8px 18px;
  color: $white;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.5px;
  background-color: $background-dark-green;
  border-radius: 32px;
}
