.footer {
  background-color: $light-grey;
}

.footer__container {
  padding: 48px 16px;
}

.footer__logo-link {
  margin-bottom: 24px;
  display: block;
}

.footer__description {
  display: block;
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
}

.footer__social-link {
  display: block;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  color: $black;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.footer__social-list {
  display: flex;
  gap: 10px;
}

.footer__navigation-link {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  color: $black;
}

.footer__navigation-link--partners {
  border-bottom: 1px solid $black;
}

.footer__lepiohin-link {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  border-bottom: 3px solid $black;
  color: $black;
  padding-bottom: 2px;
}

.footer__year {
  display: block;
  border-bottom: none;
  color: $black;
  padding-top: 4px;
}
