.offer-email__wrapper {
  background-color: $background-green;
  padding: 32px 37px 24px 16px;
  margin-bottom: 34px;
}

.offer-email__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin: 0 0 8px 0;
  color: $light-grey;
}

.offer-email__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin: 0 0 24px 0;
  color: $light-grey;
}

.offer-email__link {
  padding: 8px 18px 8px 8px;
  border-radius: 12px 32px 32px 12px;
  background-color: $light-grey;
  color: $black;
  display: block;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.5px;
}
