.intro__wrapper {
  padding: 24px 16px;
  background-color: $background-grey;
  border-radius: 16px;
  margin-bottom: 64px;
}

.intro__title {
  color: $black;
  font-size: 28px;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: -0.7px;
}

.intro__img {
  display: block;
  margin-bottom: 36px;
}

.intro__link {
  padding: 11px 16px;
  border-radius: 12px 32px 32px 12px;
  background-color: $background-dark-green;
  display: block;
  color: $white;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
}
