.for-other-questions {
  display: none;
}

.for-other-questions__wrapper {
  padding: 60px 64px 65px 64px;
  border-radius: 24px;
  background-color: $background-grey;
}

.for-other-questions__title {
  font-size: 56px;
  line-height: 64px;
  letter-spacing: -1px;
  margin-bottom: 24px;
}

.for-other-questions__description {
  display: block;
  font-family: "Manrope";
  font-size: 32px;
  font-weight: 400;
  line-height: 40px;
}

.for-other-questions__link {
  color: inherit;
  text-decoration: underline;
}
