.round-the-clock-type__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  color: $light-grey;
  padding: 24px;
  background-color: $background-dark-green;
  border-radius: 20px 20px 0px 0px;
}

.round-the-clock-type {
  background-color: $background-turquoise;
  border-radius: 20px 20px 20px 20px;
  padding-bottom: 24px;
}

.round-the-clock-type__list {
  padding: 24px 16px 16px 16px;
}

.round-the-clock-type__item {
  font-family: "Manrope";
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  padding-left: 27px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 12px;
    left: 12px;
    width: 5px;
    height: 5px;
    background-color: $black;
    border-radius: 50%;
  }
}

.round-the-clock-type__link {
  border-radius: 32px;
  padding: 11px 24px;
  text-align: center;
  background-color: $white;
  display: block;
  width: 240px;
  margin: 0 auto;
  color: $black;
}
