.sefety {
  margin-bottom: 64px;
}

.sefety__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  text-align: center;
  margin: 0 0 12px 0;
}

.sefety__list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.sefety__item {
  padding: 24px 16px;
  border-radius: 16px;
  background-color: $background-grey;
  width: 100%;
}

.sefety__item-title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 4px;
}

.sefety__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin-bottom: 24px;
}
