.forbidden {
  margin-bottom: 24px;
}

.forbidden__wrapper {
  padding: 24px 16px 13px 16px;
  background-color: $background-beige;
  border-radius: 16px;
}

.forbidden__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 16px;
}

.forbidden__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.forbidden__item-title {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin-bottom: 8px;
}

.forbidden__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
}
