.questions {
  margin-bottom: 64px;
}

.questions__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  margin-bottom: 12px;
  text-align: center;
}

.questions__list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.questions__item {
  background-color: $light-grey;
  border-radius: 16px;
}

.questions__subject {
  padding: 16px;
  display: flex;
  justify-content: space-between;
}

.questions__answer {
  display: none;
  padding: 24px 16px;
  border-top: 1px solid $background-grey;
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
}
