.features {
  margin-bottom: 64px;
}

.features__title {
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -1px;
  text-align: center;
  margin-bottom: 12px;
}

.features__list {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.features__img {
  display: block;
  margin-bottom: 16px;
}

.features__item-title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 8px;
}

.features__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
}
