.case {
  padding: 24px 16px;
  border-radius: 16px 16px 32px 16px;
  background-color: $background-grey;

  &--plug {
    background-color: $white;
  }
}

.case__title {
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin: 0 0 4px 0;
}

.case__description {
  font-family: "Manrope";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  margin: 0 0 24px 0;
}
